@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "sub";
  src: 
 url("./utils/Boska-MediumItalic.otf") format("opentype");
}

@font-face {
  font-family: "Luxora";
  src:url("./utils/LuxoraGrotesk-Light.ttf") format("truetype");
}

.sub-text {
  font-family: "Luxora" ;
}
.text {
  font-family: "sub";
  
  font-size: 6rem; 
  line-height: 4.5rem;
}

@media (min-width: 640px) {
  .text {
    font-size: 5rem; 
    line-height: 4rem;
  }
}

@media (min-width: 768px) {
  .text {
    font-size: 7rem; 
    line-height: 5rem;
  }
}

@media (min-width: 1024px) {
  .text {
    font-size:7rem; 
    line-height: 6rem;
  }
}



@media (min-width: 1536px) {
  .text {
    font-size: 9rem;
    line-height: 7rem;
  }
}
